.photo-card {
  position: relative;
  padding-top: 24px;
  padding-bottom: 25px;
  background-color: @white;
}

.photo-card::before {
  @media (max-width: @mobile-width-only) {
    content: "";
    top: 0;
    left: -20px;
    position: absolute;
    width: 320px;
    height: 1px;
    background-color: @grey1;
  }
}

.photo-card:last-child::after {
  @media (max-width: @mobile-width-only) {
    content: "";
    bottom: 0;
    left: -20px;
    position: absolute;
    width: 320px;
    height: 1px;
    background-color: @grey1;
  }
}

.photo-card__name {
  font-size: 14px;
  font-weight: 700;
  line-height: 21px;
  text-transform: uppercase;
  color: @black;

  @media (min-width: @desktop-width) {
    color: @white;
  }
}

.photo-card__author {
  color: @darkgray;

  @media (min-width: @desktop-width) {
    color: @white;
  }
}

.photo-card__button {
  border: none;
  width: 23px;
  height: 21px;
  margin-right: 14px;
  background-image: url("../img/gallery/like.svg");
  background-repeat: no-repeat;
  background-color: @white;

  @media (min-width: @tablet-width) {
    background-color: @lightgray;
    margin-right: 9px;
  }

  @media (min-width: @desktop-width) {
    background-image: url("../img/gallery/likelight.svg");
    background-repeat: no-repeat;
    background-color: @black;
    opacity: 0.3;
  }
}

.photo-card__result {
  display: block;
  font-size: 14px;
  font-weight: 700;
  line-height: 21px;
  text-transform: uppercase;
  color: @darkgray;

  @media (min-width: @desktop-width) {
    color: @white;
  }
}

.photo-card__description {
  display: flex;
  flex-direction: column;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 22px 20px 15px;
    background-color: @lightgray;
  }

  @media (min-width: @desktop-width) {
    position: absolute;
    bottom: -5px;
    width: 100%;
    padding: 17px 25px 20px;
    background-color: @black;
    opacity: 0.6;
  }
}

.photo-card__description--dark {
  @media (min-width: @tablet-width) {
    background-color: @lightgray2;
  }
}

.photo-card__description--dark .photo-card__button {
  @media (min-width: @tablet-width) {
    background-color: @lightgray2;
  }
}

.photo-card__text {
  margin-top: 15px;
  margin-bottom: 11px;
  display: flex;
  flex-direction: column;
}

.photo-card__like {
  display: flex;

  @media (min-width: @tablet-width) {
    justify-content: center;
    align-items: center;
  }
}
